.CompanyList {
    display: flex;
    flex-direction: column;
	overflow-y: auto;
    height: 100%;
    text-align: center;
    color: orange;
}

.CompanyList .container {
    display: grid;
    height: 100%;
    align-content: center;
    grid-template-columns: auto auto auto;
    gap: 10px;
    padding: 10px;
}
