.CompanyCard {
    background-color: #f38695;
    width: 300px;
    height: 350px;
    display: grid;
    align-content: center;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
}
.CompanyCard .card{
    position: relative;
    width: 300px;
    height: 350px;
    background: #fff;
    margin: 0 auto;
    border-radius: 4px;
    box-shadow:0 2px 10px rgba(0,0,0,.2);
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
}
.CompanyCard .card:before,
.CompanyCard .card:after
{
    content:"";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    background: #fff;
    transition: 0.5s;
    z-index:-1;
}
.CompanyCard .card:hover:before{
    transform: rotate(20deg);
    box-shadow: 0 2px 20px rgba(0,0,0,.2);
}
.CompanyCard .card:hover:after{
    transform: rotate(10deg);
    box-shadow: 0 2px 20px rgba(0,0,0,.2);
}
.CompanyCard .card .imgBx{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    background: #222;
    transition: 0.5s;
    z-index: 1;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
}

.CompanyCard .card:hover .imgBx
{
    bottom: 90px;
}

.CompanyCard .card .imgBx img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
}

.CompanyCard .card .details{
    position: absolute;
    background-color: darkred;
    left: 0px;
    right: 0px;
    bottom: 0px;
    height: 90px;
    text-align: center;
}

.CompanyCard .card .details h1{
    margin: 0;
    padding: 0;
    font-weight: 600;
    font-size: 18px;
    color: orange;
    text-transform: uppercase;
} 

.CompanyCard .card .details p{
    font-weight: 500;
    font-size: 14px;
    color: yellow;
    display: block;
    margin-top: 5px;
}

.CompanyCard .card .buttons {
    justify-content: space-evenly;
    transform: scale(1.0);
}