.DeleteCompany {
    height: 100%;
	display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.DeleteCompany .box h2 {
    font-size: 30px;
    color: yellow;
}

.DeleteCompany .box p {
    color: orange;
}

.DeleteCompany .box span {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: flex-end;
    flex-direction: row;
}

.DeleteCompany .box .cross {
    color: red;
}
.DeleteCompany .box .cross:hover {
    transform: scale(1.5);
}

.DeleteCompany .box .check {
    color: green;
}
.DeleteCompany .box .check:hover {
    transform: scale(1.5);
}
